.page {
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: #3e3e3e;

  &__body {
    margin: 0;
  }

  // Эта часть используется скриптом для скрытия вертикального скролла в меню
  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {

    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }

    &:first-child {
      margin-top: 64px;

      @include onDesktop {
        margin-bottom: 104px;
      }
    }
  }

  &__section-title {
    font-weight: bold;
    font-size: 36px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -1.5px;
    color: #000;
    margin: 0 0 32px;

    @include onDesktop {
      margin-bottom: 56px;
      font-size: 48px;
      line-height: 48px;
    }

    &:after {
      content: "";
      background-image: url(../images/crown.svg);
      display: block;
      background-size: contain;
      background-repeat: no-repeat;
      height: 16px;
      width: 23px;
      margin: 4px auto 0;
    }
  }
}
